<template>
  <div id="root">
    <div class="todo-container">
      <div class="todo-wrap">
        <MyHeader @addTodo="addTodo" />
        <!-- 父组件App给list传参 -->
        <MyList :todos="todos" />
        <MyFooter
          :todos="todos"
          :isShow="isShow"
          @checkAllTodo="checkAllTodo"
          @clearAllTodo="clearAllTodo"
        />
      </div>
    </div>
  </div>
</template>

<script>
import pubsub from "pubsub-js";
import MyHeader from "./components/MyHeader.vue";
import MyList from "./components/MyList.vue";
import MyFooter from "./components/MyFooter.vue";

export default {
  name: "App",
  components: { MyHeader, MyList, MyFooter },
  data() {
    return {
      //由于todos是MyHeader组件和MyFooter组件都在使用，所以放在App中（状态提升
      todos: JSON.parse(localStorage.getItem("todos")) || [],
      isShow: JSON.parse(localStorage.getItem("isShow")) || false,
    };
  },
  methods: {
    //添加一个TODO
    addTodo(todoObj) {
      // console.log(todoObj);
      this.todos.unshift(todoObj);
    },
    //勾选or取消勾选一个TODO
    checkTodo(id) {
      this.todos.forEach((todo) => {
        var trueArray = [];
        if (todo.id === id) {
          //如果我点击时,它处于未勾选状态
          if (todo.done == false) {
            //让他变为true,处于勾选状态,让按钮处于显示状态
            todo.done = !todo.done;
            this.isShow = todo.done;
          }
          //如果我点击时,它已经处于勾选状态√  准备->空
          else if (todo.done == true) {
            //先让他变为false,处于未勾选状态,
            todo.done = !todo.done;
            //再来判断变为空的时候是否要保留按钮,把每个对象的done的状态存进一个数组里
            this.todos.forEach((todo, index) => {
              trueArray.push(this.todos[index].done);
            });
            // 如果取消勾选的时候,有一个done的状态是true,则继续显示按钮,否则隐藏按钮
            if (trueArray.indexOf(true) != -1) {
              this.isShow = true;
            } else {
              this.isShow = false;
            }
          }
        }
      });
    },
    //更新一个TODO
    updateTodo(id, title) {
      this.todos.forEach((todo) => {
        if (todo.id === id) {
          todo.title = title;
        }
      });
    },
    //删除
    deleteTodo(_, id) {
      this.todos = this.todos.filter((todo) => {
        return todo.id !== id;
      });
    },
    //全选or全不选
    checkAllTodo(done) {
      this.todos.forEach((todo) => {
        todo.done = done;
        this.isShow = todo.done;
      });
    },
    //清楚所有已经完成的todo
    clearAllTodo() {
      if (this.todos.length > 0 && confirm("确定清除已完成的吗?")) {
        this.todos = this.todos.filter((todo) => {
          this.isShow = false;
          return !todo.done;
        });
      }
      console.log(this.isShow);
    },
  },
  watch: {
    todos: {
      deep: true,
      handler(newvalue, oldvalue) {
        localStorage.setItem("todos", JSON.stringify(newvalue));
      },
    },
    isShow(newvalue, oldvalue) {
      localStorage.setItem("isShow", newvalue);
    },
  },
  mounted() {
    this.$bus.$on("checkTodo", this.checkTodo);
    this.$bus.$on("updateTodo", this.updateTodo);
    // this.$bus.$on("deleteTodo", this.deleteTodo);
    this.pubId = pubsub.subscribe("deleteTodo", this.deleteTodo);
  },
  beforeDestroy() {
    this.$bus.$off("checkTodo");
    this.$bus.$off("updateTodo");
    // this.$bus.$off("deleteTodo");
    pubsub.unsubscribe(this.pubId);
  },
};
</script>

