<template>
  <transition
    :appear="true"
    name="animate__animated animate__bounce"
    enter-active-class="animate__bounceInRight"
    leave-active-class="animate__bounceOutRight"
  >
    <li>
      <label>
        <input
          type="checkbox"
          :checked="todo.done"
          @change="handleCheck(todo.id)"
        />
        <!--   
      下面这种v-model方式不推荐,会改变props,虽然vue检测不到,不会报错.
      <input type="checkbox" v-model="todo.done" /> 
      -->
        <span v-show="!todo.isEdit">{{ todo.title }}</span>
        <input
          type="text"
          v-show="todo.isEdit"
          :value="todo.title"
          @keyup.enter="handleBlur(todo, $event)"
          @blur="handleBlur(todo, $event)"
          ref="inputTitle"
        />
      </label>
      <button class="btn btn-danger" @click="handleDelete(todo.id)">
        删除
      </button>
      <button
        v-show="!todo.isEdit"
        class="btn btn-editor"
        @click="handleEdit(todo)"
      >
        编辑
      </button>
    </li>
  </transition>
</template>

<script>
import "animate.css";
import pubsub from "pubsub-js";
export default {
  name: "MyItem",
  props: ["todo"],
  data() {
    return {};
  },
  methods: {
    handleCheck(id) {
      // 通知App将对应的TODO对象的done值取反
      // this.checkTodo(id);
      this.$bus.$emit("checkTodo", id);
    },
    handleDelete(id) {
      if (confirm("确定删除吗?")) {
        // this.deleteTodo(id);
        // this.$bus.$emit("deleteTodo", id);
        pubsub.publish("deleteTodo", id);
      }
    },
    //编辑功能
    handleEdit(todo) {
      //判断 如果此时的todo有isEdit属性,就不要再添加了,直接在这个属性上更改即可
      if (todo.hasOwnProperty("isEdit")) {
        todo.isEdit = true;
      } else {
        //  如果此时的todo没有isEdit属性,则用$set方法给todo加进去isEdit属性
        this.$set(todo, "isEdit", true);
      }
      /*  setTimeout(() => {
        this.$refs.inputTitle.focus();
      }, 200); */
      //改用官方API,类似定时器,会暂时跑到其他队列,就不会立即执行 --- this.$nextTick(回调函数)它指定的回调函数会在DOM节点更新之后再执行
      this.$nextTick(function () {
        this.$refs.inputTitle.focus();
      });
    },
    //编辑栏失去焦点时完成输入,此时的todo对象已经有了isEdir这个属性,并且是动态的所以直接改为false,就好了
    //这个也是真正将编辑栏里的值进行替换的修改逻辑
    handleBlur(todo, e) {
      todo.isEdit = false;
      //如果用户二次编辑输入改为空,就警告并return终止
      if (!e.target.value.trim()) {
        return alert("输入不能修改为空!");
      }
      this.$bus.$emit("updateTodo", todo.id, e.target.value);
    },
  },
};
</script>

<style scoped>
/*item*/
li {
  list-style: none;
  height: 36px;
  line-height: 36px;
  padding: 0 5px;
  border-bottom: 1px solid #ddd;
}

li label {
  float: left;
  cursor: pointer;
}

li label li input {
  vertical-align: middle;
  margin-right: 6px;
  position: relative;
  top: -1px;
}

li button {
  float: right;
  display: none;
  margin-top: 3px;
}

li:before {
  content: initial;
}

li:last-child {
  border-bottom: none;
}

li:hover {
  background-color: #ddd;
}

li:hover button {
  display: block;
}
</style>